/*fuentes*//*header*//*Popup*//*social media*/.header {
  width: 100%;
  height: 70px;
  background-color: #664596;
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
  margin: 0;
}

.home {
  width: 25%;
  height: 25%;
  margin: auto;
  display: block;
}

.button-burger {
  width: 25%;
  height: 25%;
  margin-left: 20px;
}

.phone-header {
  width: 50%;
  font-size: 12px;
}

.main-home {
  display: none;
}

.burger {
  position: relative;
}

.menu {
  position: absolute;
  list-style: none;
  top: -10%;
  left: -100%;
  transition: top ease 2s;
}

.hidden {
  display: none;
}

.new-position {
  left: -10%;
}

#popup {
  visibility: hidden;
  opacity: 0;
  margin-top: -300px;
}

#popup:target {
  visibility: visible;
  opacity: 1;
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  z-index: 999;
  transition: all ease 1s;
}

.popup-contenedor {
  position: relative;
  margin: 25% auto;
  padding: 30px 50px;
  background-color: #664596;
  color: #FFA93B;
  border-radius: 3px;
  width: 60%;
}

a.popup-close {
  position: absolute;
  top: 3px;
  right: 3px;
  background-color: #FFA93B;
  padding: 7px 10px;
  font-size: 20px;
  text-decoration: none;
  line-height: 1;
  color: #fff;
  border-radius: 3px;
}

body {
  font-family: 'Abel', sans-serif;
  font-size: 16px;
}

.title {
  font-family: 'Lilita One', cursive;
  font-size: 20px;
}

.link--cover {
  text-decoration: none;
}

.button {
  background-color: blue;
}

.page-quiz {
  background-color: grey;
}

.instructions-initial-pop-up {
  background-color: pink;
}

.button--quiz {
  background-color: tomato;
}

.phrase-box {
  background-color: yellow;
}

.resource {
  border: 1px solid black;
  margin: 15px;
  width: 100%;
  box-sizing: border-box;
}
